.container {
  margin: 0 auto;
  
  @media screen and (min-width: 320px) and (max-width: 767px) {
    min-width: 320px;
    max-width: 480px;
    padding: 0 20px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 34px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding: 0 115px;
  }
}
